.mediaImage_modal {
  max-width: none;
  min-width: auto;
  min-height: auto;
  padding: 1.5rem;
  height: 60%;
  width: auto;
}

.mediaImage_modal >img {
  /* display: block;
  width: 100%;
  height: 87px;
  object-fit: cover; */
  display: block;
  width: 121%;
  height: 108px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: var(--boxShadow);
}

.headerSong {
  text-align: right;
  font-size: 1.5rem;
  
  padding: 0.2rem 1.3rem;
  color: var(--colorText);
  background-color: var(--colorBackgroundAll);
  border-radius: 0.5rem;
  /* border: 2px solid var(--titleSongColor); */
  /* border-bottom: 4px solid #b2b1b1; */
  border: 1px solid  white;
  text-shadow: -1px -1.6px 0px #151616;
  box-shadow: var(--boxShadow);
  cursor: pointer;
  padding: 0 1em 0 1em;
}
.headerSong p {
  margin: 0%;
  /* padding: 0 1em 0 1em; */
}

.linkTo {
  position: absolute;
  top: 97%;
  left: 31%;
  background-color: rgb(211 24 24);
  border: none;
  color: white;
  padding: 2px 6px;
  font-size: 14px;
  cursor: pointer;
  border-radius: .5em;
  z-index: 7;
}

.audioBlock {
  display: block;
}

.audioBlock p {
  font-size: 1.5rem;
  text-align: center;
  color: var(--colorText);
  /* padding: 2px 6px; */
  /* padding: 0 1em 0 1em; */
  /* margin: 5em; */
  /* background-color: var(--tributeAfterBefore); */
  background-color: var(--buttonBack);
  border-radius: 0.5em;
  text-shadow: var(--textShadow); 
   box-shadow: var(--boxShadow);
   border: 1px solid  white;
}

.videoBlock p {
  /* display: block; */
  font-size: 1.5rem;
  text-align: center;
  color: var(--colorText);
  background-color: var(--buttonBack);
  border-radius: 0.5em;
  border: 1px solid  white;
  text-shadow: var(--textShadow);
  box-shadow: var(--boxShadow);
}

.videoBlock {
  position: relative;

  width: 100%;
  /* height: 255px; */

}

#videoFrame {
  position: relative;
  
  width: 100% !important;
  height: 22vh !important;
  border-width: 0;
  outline-width: 0;
}

audio::-webkit-media-controls-enclosure {

  background-color: #fffffff5;
  text-shadow: -1px -1.6px 0px #e4e0e7;
  /* box-shadow: var(--boxShadow); */
  border: 5px solid  var(--buttonBack);;

}



